(function(){
  'use strict';

  angular.module('app.view.test').controller('TestController', TestController);

  /*@ngInject */
  function TestController($state){
    var vm = this;
    vm.testTitle = "Sample Test title";
    vm.loginClick = loginClick;
    vm.homeClick = homeClick;
    vm.deleteQueuedID = deleteQueuedID;

   ////////////////
   vm.list = [
     {id:'0', name: 'John', number: '+12345780', isVegan: 'Yes'},
     {id:'1', name: 'John', number: '+12345780', isVegan: 'No'},
     {id:'2', name: 'John', number: '+12345780', isVegan: 'Yes'},
     {id:'3', name: 'John', number: '+12345780', isVegan: 'No'},
     {id:'4', name: 'John', number: '+12345780', isVegan: 'Yes'},
     {id:'5', name: 'John', number: '+12345780', isVegan: 'No'},
     {id:'6', name: 'John', number: '+12345780', isVegan: 'No'},
     {id:'7', name: 'John', number: '+12345780', isVegan: 'Yes'},
     {id:'8', name: 'John', number: '+12345780', isVegan: 'Yes'},
     {id:'9', name: 'John', number: '+12345780', isVegan: 'Yes'},
     {id:'10', name: 'John', number: '+12345780', isVegan: 'No'},
     {id:'11', name: 'John', number: '+12345780', isVegan: 'No'},
     {id:'12', name: 'John', number: '+12345780', isVegan: 'Yes'},
     {id:'13', name: 'John', number: '+12345780', isVegan: 'No'}
   ]

   function loginClick() {
       $state.go('skeleton-blank.login');
   }

   function homeClick() {
       $state.go('home');
   }

   function deleteQueuedID(item){
     var newDataList=[];
     var oldDataList = vm.list;

      angular.forEach(oldDataList,function(v){
        if(v.id != item.id){
            newDataList.push(v);
        }
      });

     vm.list = newDataList;
     console.log(item.id)
    }
  }
})();

(function(){
  'use strict';

  angular.module('app.view.test').config(moduleConfig);

  /*@ngInject */
  function moduleConfig($stateProvider){

    $stateProvider
            .state('skeleton.admin.profile', {
                url: '/admin/profile',
                templateUrl: 'app/components/admin/views/test/test.html',
                controller: 'TestController',
                controllerAs: 'vm'
            });
  }
})();

