(function() {
  'use strict';

  angular
    .module('skeleton')
    .directive('menuBar', menuBar);

  /** @ngInject */
  function menuBar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/elements/menubar/menubar.html',
      scope: {
          creationDate: '='
      },
      controller: MenubarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function MenubarController(jQuery, Stellar, moment) {
      window.onload = _init();

      var vm = this;
    }

    //Init JS components
    function _init() {
          //Initialize Stellar
          jQuery(window).stellar();

          //Initialize niceScroll
          jQuery("html").niceScroll({
              cursorcolor:"rgba(30,30,30,.5)",
              zindex:999,
              scrollspeed:100,
              mousescrollstep:50,
              cursorborder:"0px solid #fff",
          });

          window.addEventListener('scroll', function(e){

      			if (window.matchMedia('(min-width: 998px)').matches) {
      				var distanceY = window.pageYOffset || document.documentElement.scrollTop,
      					shrinkOn = 0,
      					header = document.querySelector("header");
      				if (distanceY > shrinkOn) {
      					classie.add(header,"smaller");
      					jQuery('header').addClass("logo-smaller");
      				} else {
      					if (classie.has(header,"smaller")) {
      						classie.remove(header,"smaller");
      						jQuery('header').removeClass("logo-smaller");
      					}

      				}
      			}
          });

      		//Revert nav-menu
      		window.onresize = function(event) {
      			var mq = window.matchMedia( "(min-width: 993px)" );
      			var mx = window.matchMedia( "(max-width: 992px)" );
      			var header = document.querySelector("header");

      			if (mq.matches) {
      				if (classie.has(header,"smaller")) {
      					classie.remove(header,"smaller");
      					jQuery('header').removeClass("logo-smaller");
      				}
      				jQuery("#mainmenu").show();
      			}

      			if (mx.matches) {
      				if(jQuery('#menu-btn').is(":hidden")){
      					if (classie.has(header,"smaller")) {
      						classie.remove(header,"smaller");
      						jQuery('header').removeClass("logo-smaller");
      					}
      					jQuery('#mainmenu').show();
      				}else{
      					jQuery('#mainmenu').hide();
      				}
      			}
      		};

      		//Menu on click event
      		jQuery('#menu-btn').on('click', function(e){
      			jQuery('#mainmenu').toggle();
      		});

      		// --------------------------------------------------
      		// Init WOW
      		// --------------------------------------------------
        	new WOW().init();
      }
    //End
  }

})();

(function() {
  'use strict';

  angular
    .module('skeleton')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '=',
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

