(function() {

  angular
    .module('skeleton')
    .directive('onFinishRender', function ($timeout) {
      return {
          restrict: 'A',
          link: function (scope, element, attr) {
              if (scope.$last === true) {
                  $timeout(function () {
                      scope.$emit('ngRepeatFinished');
                  });
              }
          }
      }
    })
    .directive('niftyWidget', niftyWidget);

  /** @ngInject */
  function niftyWidget() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/elements/isotope/isotope.html',
      scope: {
          creationDate: '='
      },
      controller: NiftyWidgetController,
      controllerAs: 'NiftyWidgetCtrl'
    };

    return directive;

    /** @ngInject */
    function NiftyWidgetController($scope, isotopeLabels, prettyPhoto, moment) {
      var vm = this;
      vm.prettyPhotoList = prettyPhoto.all();
      vm.isotopeLabelsList = isotopeLabels.all();

      $scope.$on('ngRepeatFinished', function (ngRepeatFinishedEvent) {
        jQuery(document).ready(function() {
          init_widget();
        });
      });
    }

    //Init JS components
    function init_widget() {
        // --------------------------------------------------
    		// prettyPhoto
    		// --------------------------------------------------
    		jQuery("area[data-type^='prettyPhoto']").prettyPhoto();
    		jQuery(".nifty-section:first a[data-type^='prettyPhoto']").prettyPhoto({
    			animation_speed: 'fast',
    			theme: 'pp_default',
    			slideshow: 3000,
    			autoplay_slideshow: false
    		});

    		jQuery(".nifty-section:gt(0) a[data-type^='prettyPhoto']").prettyPhoto({
    			animation_speed: 'fast',
    			slideshow: 10000,
    			hideflash: true
    		});

        // --------------------------------------------------
        // Portfolio
        // --------------------------------------------------
        jQuery('.item .item-frame').each(function() {

          jQuery(this).find("img").css("width", "100%");
          jQuery(this).find("img").css("height", "auto");

          jQuery(this).find("img").on('load', function() {
            w = jQuery(this).css("width");
            h = jQuery(this).css("height");

            jQuery(this).parent().css("height", h);

          }).each(function() {
            if (this.complete) jQuery(this).load();
          });
        });

        // --------------------------------------------------
        // portfolio hover
        // --------------------------------------------------
        jQuery('.overlay').fadeTo(1, 0);

        jQuery(".item .item-frame").hover(

        function() {
          jQuery(this).parent().find(".overlay").width(jQuery(this).find("img").css("width"));
          jQuery(this).parent().find(".overlay").height(jQuery(this).find("img").css("height"));
          jQuery(this).parent().find(".overlay").stop(true).fadeTo(300, .9);
          picheight = jQuery(this).find("img").css("height");
          newheight = (picheight.substring(0, picheight.length - 2) / 2) - 48;

          jQuery(this).parent().find(".icon").stop(true).animate({
            'margin-top': newheight
          }, 300, 'easeOutCubic');

          w = jQuery(this).find("img").css("width");
          h = jQuery(this).find("img").css("height");
          w = parseInt(w, 10);
          h = parseInt(h, 10);
          $scale = 1.2;

          jQuery(this).find("img").stop(true).animate({
            width: w * $scale,
            height: h * $scale,
            'margin-left': -w * ($scale - 1) / 2,
            'margin-top': -h * ($scale - 1) / 2
          }, 700, 'easeOutQuad');

        },

        function() {
          jQuery(this).parent().find(".icon").stop(true).animate({
            'margin-top': newheight + 100
          }, 300, 'easeOutCubic');
          jQuery(this).parent().find(".overlay").stop(true).fadeTo(300, 0);
          jQuery(this).find("img").stop(true).animate({
            width: '100%',
            height: '100%',
            'margin-left': 0,
            'margin-top': 0
          }, 700, 'easeOutQuad');
        });


        jQuery('.overlay').fadeTo(1, 0);

        // --------------------------------------------------
        // Init nifty-section
        // --------------------------------------------------
        var $container = jQuery('#nifty-section');
        $container.isotope({
          itemSelector: '.item',
          filter: '*',
        });

        jQuery('#filters a').click(function() {
          var $this = jQuery(this);
          if ($this.hasClass('selected')) {
            return false;
          }
          var $optionSet = $this.parents();
          $optionSet.find('.selected').removeClass('selected');
          $this.addClass('selected');

          var selector = jQuery(this).attr('data-filter');
          $container.isotope({
            filter: selector,
          });
          return false;
        });

        // --------------------------------------------------
        // Load nifty-section loader
        // --------------------------------------------------
        jQuery('.nifty-item .item .icon-info').click(function() {
            jQuery('.page-overlay').show();
            url = jQuery(this).attr("data-value");
            jQuery("#loader-area .project-load").load(url, function() {
              jQuery("#loader-area").slideDown(500, function() {
                jQuery('.page-overlay').hide();
                jQuery('html, body').animate({
                  scrollTop: jQuery('#loader-area').offset().top - 70
                }, 500, 'easeOutCirc');

                jQuery(".image-slider").owlCarousel({
                  items: 1,
                  singleItem: true,
                  navigation: false,
                  pagination: true,
                  autoPlay: false,
                });

                jQuery(".project-view").fitVids();

                jQuery('#btn-close-x').click(function() {
                  jQuery("#loader-area").slideUp(500, function() {
                    jQuery('html, body').animate({
                      scrollTop: jQuery('#section-portfolio').offset().top - 70
                    }, 500, 'easeOutCirc');
                  });

                  return false;

                });

              });
          });
        });
    }
    //End

}

})();

